import React, { useEffect, useState } from 'react';
import { Box, Typography, Button, Card, CardContent } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Bar } from 'react-chartjs-2';
import { Chart, registerables } from 'chart.js';

const api_url = "http://localhost:3000";

Chart.register(...registerables);

function Dashboard() {
  const navigate = useNavigate();
  const [userData, setUserData] = useState(null); //  null 

  useEffect(() => {
    //     
    const user = localStorage.getItem('user');
    if (user) {
      setUserData(JSON.parse(user));
    } else {
      //      
      navigate('/signin');
    }
  }, [navigate]);

  const handleLogout = async () => {
    if (!userData) {
      console.error('      .');
      return;
    }

    //   
    try {
      console.log('  ...');
      console.log('  :', userData);

      const response = await axios.post(`${api_url}/api/logout`, {
        record_no: userData.record_no,
      }, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (response.status === 200) {
        console.log(' , :', response.data);
        alert(' ');
        //          
        localStorage.removeItem('user');
        setUserData(null); //  

        //     
        setTimeout(() => {
          navigate('/');
          console.log('   ...');
        }, 100);
      } else {
        console.error(' ,  :', response.status);
      }
    } catch (error) {
      console.error('     :', error);
      alert('   .');
    }
  };

  //   
  const data = {
    labels: [' ', ' '],
    datasets: [
      {
        label: ' ',
        data: [userData?.CountLoginSuccess || 0, userData?.CountLoginFail || 0],
        backgroundColor: ['rgba(75, 192, 192, 0.6)', 'rgba(255, 99, 132, 0.6)'],
        borderColor: ['rgba(75, 192, 192, 1)', 'rgba(255, 99, 132, 1)'],
        borderWidth: 1,
      },
    ],
  };

  //   
  const options = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  if (!userData) {
    return null; //      
  }

  return (
    <Box
      sx={{
        minHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        paddingTop: 8,
        gap: 4,
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'flex-start',
          gap: 6,
        }}
      >
        <Card sx={{ maxWidth: 400, width: '100%', boxShadow: 3 }}>
          <CardContent>
            <Typography variant="h4" gutterBottom>
               
            </Typography>
            <Typography variant="h6" gutterBottom>
              : {userData.name}
            </Typography>
            <Typography variant="h6" gutterBottom>
              : {userData.email}
            </Typography>
          </CardContent>
        </Card>
        <Box sx={{ maxWidth: '600px' }}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <Bar data={data} options={options} />
        </Box>
      </Box>
      <Button variant="contained" color="secondary" onClick={handleLogout} sx={{ mt: 3 }}>
        
      </Button>
    </Box>
  );
}

export default Dashboard;
