import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import FormLabel from '@mui/material/FormLabel';
import FormControl from '@mui/material/FormControl';
import Link from '@mui/material/Link';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import MuiCard from '@mui/material/Card';
import { createTheme, ThemeProvider, styled } from '@mui/material/styles';
import { Link as RouterLink } from 'react-router-dom';
import axios from 'axios';
const api_url = "http://localhost:3000";

const Card = styled(MuiCard)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignSelf: 'center',
  width: '100%',
  padding: theme.spacing(4),
  gap: theme.spacing(2),
  margin: 'auto',
  [theme.breakpoints.up('sm')]: {
    maxWidth: '450px',
  },
}));

const SignUpContainer = styled(Stack)(({ theme }) => ({
  minHeight: '100vh',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  padding: theme.spacing(2),
  [theme.breakpoints.up('sm')]: {
    padding: theme.spacing(4),
  },
  backgroundImage:
    'radial-gradient(ellipse at 50% 50%, hsl(210, 100%, 97%), hsl(0, 0%, 100%))',
  backgroundRepeat: 'no-repeat',
}));

export default function SignUp() {
  const [formData, setFormData] = React.useState({
    id: '',
    name: '',
    password: '',
    email: ''
  });

  const [idMessage, setIdMessage] = React.useState('');
  const [emailMessage, setEmailMessage] = React.useState('');

  const [nameError, setNameError] = React.useState(false);
  const [idError, setIdError] = React.useState(false);
  const [passwordError, setPasswordError] = React.useState(false);
  const [emailError, setEmailError] = React.useState(false);

  const idInputRef = React.useRef();
  const emailInputRef = React.useRef();

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const validateInputs = () => {
    let isValid = true;

    if (!formData.id) {
      setIdError(true);
      setIdMessage(' .');
      isValid = false;
    } else {
      setIdError(false);
      setIdMessage('');
    }

    if (!formData.password || formData.password.length < 6) {
      setPasswordError(true);
      isValid = false;
    } else {
      setPasswordError(false);
    }

    if (!formData.name) {
      setNameError(true);
      isValid = false;
    } else {
      setNameError(false);
    }

    if (!formData.email || !/\S+@\S+\.\S+/.test(formData.email)) {
      setEmailError(true);
      setEmailMessage('   .');
      isValid = false;
    } else {
      setEmailError(false);
      setEmailMessage('');
    }

    return isValid;
  };



  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!validateInputs()) {
      return;
    }
  
    try {
      const checkIdAndEmail = await axios.post(`${api_url}/api/checkId`, {
        id: formData.id,
        email: formData.email
      }, {
        headers: {
          'Content-Type': 'application/json'
        }
      });
  
      if (checkIdAndEmail.status === 409) {
        const message = checkIdAndEmail.data.message;
  
        //   
        if (message.includes('  .')) {
          setIdError(true);
          setIdMessage('  .');
          setFormData(prevState => ({ ...prevState, id: '' }));  //   
        } else {
          setIdError(false);
          setIdMessage('');
        }
  
        //   
        if (message.includes('  .')) {
          setEmailError(true);
          setEmailMessage('  .');
          setFormData(prevState => ({ ...prevState, email: '' }));  //   
        } else {
          setEmailError(false);
          setEmailMessage('');
        }
  
        return; //         .
      }
  
      
  
      //  
      const response = await axios.post(`${api_url}/api/signup`, formData, {
        headers: {
          'Content-Type': 'application/json'
        }
      });
  
      if (response.status === 200) {
        alert(' !');
      } else {
        console.error(' : ', response.data.message);
      }
    } catch (error) {
      console.error('   :', error);
      if (error.response) {
        if (error.response.status === 409) {
          const message = error.response.data.message;
          if (message.includes('')) {
            setIdError(true);
            setIdMessage(message);
          } else if (message.includes('')) {
            setEmailError(true);
            setEmailMessage(message);
          }
        } else {
          console.error('', error.response.data);
        }
      } else if (error.request) {
        console.error('   ', error.request);
      } else {
        console.error(' : ', error.message);
      }
    }
  };
  
          

  return (
    <ThemeProvider theme={createTheme()}>
      <CssBaseline />
      <SignUpContainer>
        <Card variant="outlined">
          <Typography component="h1" variant="h4">
            Sign up
          </Typography>
          <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <FormControl>
              <FormLabel htmlFor="name"></FormLabel>
              <TextField
                autoComplete="name"
                name="name"
                required
                fullWidth
                id="name"
                value={formData.name}
                onChange={handleChange}
                placeholder=""
                error={nameError}
              />
            </FormControl>
            <FormControl>
              <FormLabel htmlFor="id"></FormLabel>
              <TextField
                required
                fullWidth
                id="id"
                placeholder="id"
                name="id"
                autoComplete="id"
                value={formData.id}
                onChange={handleChange}
                inputRef={idInputRef}
                variant="outlined"
                error={idError}
                helperText={idMessage}
              />
            </FormControl>
            <FormControl>
              <FormLabel htmlFor='email'>Email</FormLabel>
              <TextField
                required
                fullWidth
                id='email'
                placeholder='your@email.com'
                name='email'
                value={formData.email}
                onChange={handleChange}
                inputRef={emailInputRef}
                autoComplete='email'
                variant='outlined'
                error={emailError}
                helperText={emailMessage}
              />
            </FormControl>
            <FormControl>
              <FormLabel htmlFor="password"></FormLabel>
              <TextField
                required
                fullWidth
                name="password"
                placeholder=""
                type="password"
                id="password"
                value={formData.password}
                onChange={handleChange}
                autoComplete="new-password"
                variant="outlined"
                error={passwordError}
              />
            </FormControl>
            <Button type="submit" fullWidth variant="contained">
              Sign up
            </Button>
            <Typography sx={{ textAlign: 'center' }}>
                ?{' '}
              <span>
                <Link component={RouterLink} to="/">
                  Sign in
                </Link>
              </span>
            </Typography>
          </Box>
        </Card>
      </SignUpContainer>
    </ThemeProvider>
  );
}
